<script>
import { mapState, mapActions } from 'vuex'
import FreetextOutlined from '@/components/freetext/Outlined.vue'
// import SwitchLang from '@/components/layouts/SwitchLang.vue'
export default {
  name: 'App',
  components: {
    FreetextOutlined// SwitchLang
  },
  data: () => ({
    activeId: 'Profile',
    value: 0,
    isMobile: false,
    isIpad: false,
    sheet: false,
    scrollNum: 0
  }),
  computed: {
    ...mapState('home', {
      menus: (state) => state.menus
    }),
    ...mapState('auth', {
      state: (state) => state
    })
  },
  mounted () {
    const theme = localStorage.getItem('dark_theme')
    if (theme) {
      if (theme === 'true') {
        this.$vuetify.theme.dark = true
      } else {
        this.$vuetify.theme.dark = false
      }
    } else {
      this.$vuetify.theme.dark = false
      localStorage.setItem('dark_theme', this.$vuetify.theme.dark.toString())
    }
    this.isMobile = window.innerWidth < 600
    this.isIpad = window.innerWidth < 900
    window.addEventListener('resize', this.checkIsMobile)
    window.addEventListener('resize', this.checkIsIpad)
    window.addEventListener('scroll', this.handleScroll)
  },
  filters: {
    upper (value) {
      if (!value) return ''
      value = value.toString()
      return value.toUpperCase()
    }
  },
  methods: {
    ...mapActions('auth', [
      'dialogFormLogin'
    ]),
    lightbulb () {
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark
      localStorage.setItem('dark_theme', this.$vuetify.theme.dark.toString())
    },
    getContact () {
      const editButtonRef = this.$refs.contact
      editButtonRef.focus()
    },
    checkIsMobile () {
      this.isMobile = window.innerWidth < 600
    },
    checkIsIpad () {
      this.isIpad = window.innerWidth < 868
    },
    handleScroll () {
      // Mendapatkan posisi scroll saat ini
      const scrollTop = window.pageYOffset || document.documentElement.scrollTop
      this.scrollNum = scrollTop
    }
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.checkIsMobile)
    window.removeEventListener('resize', this.checkIsIpad)
  }
}
</script>
