<template>
  <div>
    <v-text-field
      primary
      :label="label"
      :append-icon="appendIcon"
      :append-outer-icon="appendOuterIcon"
      outlined
      dense
      class="block"
      v-model="model"
      @click:append-outer="sendMessage"
    ></v-text-field>
  </div>
</template>

<script>
export default {
  name: 'FreetextOutlined',
  data () {
    return {
      model: this.value
    }
  },
  props: {
    label: {
      type: String,
      required: false,
      default: 'Search'
    },
    appendOuterIcon: {
      type: String,
      required: false,
      default: 'mdi-send'
    },
    appendIcon: {
      type: String,
      required: false,
      default: ''
    },
    value: {
      required: false
    }
  },
  methods: {
    sendMessage () {
      this.$emit('input', this.model)
    }
  }
}
</script>
